h1 {
  text-align: center;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Button{
    width: 100%;   
    margin: 0 auto;
    margin-left: 825px;
    display: flex;
    justify-content: space-between;
}

@media screen and (max-width: 480px) {
    .Button {
        margin-left: 0px;
    }         
}

@media screen and (max-width: 780px) {
    .Button {
        margin-left: 150px;
    }         
}

@media screen and (max-width: 1024px) {
    .Button {
        margin-left: 310px;
    }
}
table{
    border-collapse: separate;
    border-spacing: 10px;
    *border-collapse: expression('separate', cellSpacing='10px');
    background: white;
    margin-left: 190px;
}

.title{
    font-weight: bold;
    margin: 25px 12px;
    font-size: 22px;
    background: white;
}

table tr{
    font-family: 'Nunito', sans-serif;
    font-style: normal;
    font-weight: bold;
    text-align: center;
    background: white;
}

.statsPrincipal td{
    margin: 12px 12px 12px 12px;
    padding: 8px;
    background: white;
}

.Stats {
    margin-left: 210px;
}

.Modal {
    border-radius: 10px;
    margin-top: 5px;
}

.ButtonModal{
    margin-left: 500px;
    margin-bottom: 5px;
}
